import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'


Vue.use(Vuex)

const store=new Vuex.Store({
	state:{
		userid:''
	},
	mutations:{
		getUser(state,res){
			state.userid=res
		}
	},
		actions:{
			getUserinfo({commit}){
//				this.$axios({
//					url:'https://test.e-shequ.com/msa/crm/oauth/getuser',
//					method:'get'
//				}).then((res) =>{
//					commit('getUser',res.data.userid)
//				})
             axios.get('https://test.e-shequ.com/msa/crm/oauth/getuser').then( (res) =>{
             	commit('getUser',res.data.userid)
             })
			}
},

   getters:{
   	userid: state=>state.userid
   }

	})

export default store


