<template>
   <div class="message" @click="toKefu">
   <div class="left">
   	<img src="../assets/img/child.jpg">
   	<mt-badge size="small" color="#df0303" class="badge" v-show="showBadge">{{data.count}}</mt-badge>
   </div>
   <div class="middle">
   	<p>报修订单</p>
   	<p style="margin-top: 15px;color: #919191;font-size: 10px;" ref="ppp">有{{data.count}}条最新的报修信息</p>
   </div>
   <div class="right">
   	<p>{{data.date}}</p>
   	
   </div>
   </div>
   

</template>

<script>
//	import {mapState} from 'vuex'
export default {
  name: 'message',
  data () {
    return {
    	data:'',
    	showBadge:false,
    	params:{
    		userId:this.$route.query.userId
    	}
    }
  },
  methods:{
  	toKefu(){
  		this.$router.push({name:'kefu',query:{userId:this.params.userId}});
  	}
  		
  	
  },
 
//computed:{
//	...mapState({
//		userid:state =>state.userid
//	})
//	
//},
  mounted(){
  			this.$axios({
  			url:'https://test.e-shequ.com/msa/crm/repairOder/getNotLookOrder?userId='+this.params.userId,
  			method:'get'
  		}).then((res) =>{
  			if(res.data.success==false){
  				this.$refs.ppp.innerHTML=res.data.message;
  				this.showBadge=false;
  				
  			}else{
  				this.data=res.data.result;
  				this.showBadge=true;
  			}
  			
  		})
 console.log(this.userid)
  },
 
// created(){
// 	this.$store.dispatch('getUserinfo');
// }
  
  
  
 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.message{
	padding: 15px 5% 15px 3%;
	height: 60px;
	background: #fff;
	border-bottom: 1px solid #b0b0b0;
}
.left{
	float:left;
	width: 60px;
	height: 60px;
	border: 1px solid #c4c4c4;
	position: relative;
}
.left img{
	width: 80%;
	height: 80%;
	border-radius: 50%;
	position: absolute;
	left: 10%;
	top: 10%;
}
.badge{
    position: absolute;
    left:46px;
    top:-8px;
    display: block;
}
.middle{
	float: left;
	text-align:left;
	width: 50%;
	margin:5px 0 10px 25px;
}
.middle p{
	font-size: 14px;
	font-family: "微软雅黑";
	color: #494949;
	letter-spacing: 1px;
	padding-left: 1px;
}
.right{
	position: absolute;
	top: 36px;
	right: 5%;
}
.right p{
	color: #494949;
	font-size: 14px;
	font-family:"微软雅黑";
}
.mint-badge.is-size-small{
	border-radius: 50%;
	width: 20px;
	height: 20px;
	line-height: 20px;
	padding: 0;
	font-size: 14px;
}	
</style>



