<template>
	<div class="wrapper">
	<div class="kefu" ref="wrapper" :style="{height:wrapperHeight+'px'}">
		<!--<div class="content">
			<p>报修订单提醒</p>
			<p style="margin-top: 10px;color: #7f7f7f;font-size: 14px;">你有一条报修信息需要处理</p>
		</div>
		<div class="detail" @click="toDingdan">
			<span class="left">详情</span>
			<span class="right"><i class="iconfont icon-custom-forward"></i></span>
		</div>-->
		<mt-loadmore  :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"
			   :auto-fill="false" :bottomPullText='bottomText'>
		<ul>
			<li v-for="(item,index) in list" @click="toDingdan(item)">
				<span class="left"><img :src="item.repairPepoleImg"></span>
				<span class="middle" style="display: block;margin-left: 76px;height: 100%;">
					<p style="float: left;margin-top: 0;font-size: 18px;color: #000;font-weight: 600;width: 100%;text-align: left;">{{item.repairPepoleName}}</p>
					<p class="right">{{item.repairDate}}</p>
				<p class="where"><i class="iconfont icon-location2"></i> {{item.repairAddress}}</p>
				</span>
				
			</li>
			
		</ul>
		
		</mt-loadmore>
	</div>
	</div>
</template>

<script>
	import { Loadmore } from 'mint-ui'
	export default{
		data(){
			return{
				axiosParams:{
					userId:this.$route.query.userId
				},
				list:[],
				repairId:'',
				pageNum:1,
				allLoaded:false,
			    bottomText:'上拉加载更多',
				wrapperHeight:0
			}
		},
		methods:{
			toDingdan(value){
				this.$router.push({name:'dingdan',query:{repairId:value.repairId}});
				
			},
			
			getLook(){
				this.$axios({
					url:'https://test.e-shequ.com/msa/crm/repairOder/updateIsLook?userId='+this.axiosParams.userId,
					method:'get'
				}).then((res) =>{
					
				})
			},
			
			loadBottom(){
				this.pageNum++;
				
		    	setTimeout(() =>{
		    		this.$axios({
				url:'https://test.e-shequ.com/msa/crm/repairOder/getRepairOrderList?userId='+this.axiosParams.userId+'&pageNum='+this.pageNum,
				method:'get'
			}).then((res) =>{
				const data=res.data.result;
				if(this.pageNum>data.pages){
					this.allLoaded=true;
				}
				else{
					this.list=this.list.concat(data.list);
				}
				
			})
			 this.$refs.loadmore.onBottomLoaded();
		    	
		    	},1000)
			},
			
			getDataform(){
			this.$axios({
				url:'https://test.e-shequ.com/msa/crm/repairOder/getRepairOrderList?userId='+this.axiosParams.userId+'&pageNum='+1,
				method:'get'
			}).then((res) =>{
				this.list=res.data.result.list;
			})
		    
	
			},
			
			getScreenheight(){
				this.wrapperHeight=document.documentElement.clientHeight-this.$refs.wrapper.getBoundingClientRect().top;
			},
		},
		mounted(){
			this.getLook();
			this.getScreenheight();
		},
		created(){
			this.getDataform();
		}
	}
</script>

<style scoped>
.wrapper{
	width: 100%;
	background-color: #fff;
}	
.kefu{
	overflow: scroll;
	-webkit-overflow-scrolling: touch;
}
.kefu ul li{
	width: 88%;
	height:68px;
	padding:15px 6%;
	border-bottom: 2px solid #dddddd;
}
.left{
	width: 66px;
	float: left;
}
.left img{
	width: 100%;
	height: 100%;
	border-radius:50%;
}
.right{
	text-align: right;
	font-size: 15px;
	float: right;
	color: #b1b1b1;
	width: 80%;
}
.where{
	font-size: 14px;
	float: left;
	text-align: left;
	width: 100%;
}
.icon-location2
{
	font-size: 22px;
	color: #000;
	font-weight: bold;
}
</style>


