import Vue from 'vue'
import Router from 'vue-router'
import message from '@/pages/message'
import kefu from '@/pages/components/kefu'
import dingdan from '@/pages/components/dingdan'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'message',
      component: message,
      meta:{
      	title:'消息中心'
      }
    },
    {
    	path:'/kefu',
    	name:'kefu',
    	component:kefu,
    	meta:{
    		title:'报修订单'
    	}
    },
    {
    	path:'/dingdan',
    	name:'dingdan',
    	component:dingdan,
    	meta:{
    		title:'订单详情'
    	}
    }
  ]
});
//路由钩子函数
//在每一次路由跳转之前会进入这个方法 to：到哪去  from：从哪来 next() 调用这个方法来完成这个钩子函数
router.beforeEach((to,from,next) =>{
	changeTitle(to.meta.title);
	next();
});

//动态的改变title
function changeTitle(title){
	title=title?title:'企业微信';
	window.document.title=title;
};

export default router



